import * as d3 from "d3";
import Shape from '../shape/index';
import core from '../utils/core';
import DatabaseEvent from '../database/databaseEvent';

class Axis extends Shape {

  // use config as data in Shape, so the board can update config
  constructor (name, config) {
    super();
    super.overrideViaConfig(config);
    this.init(name, config);
    this.setGenerator(); // call every render in Axis
  }

  init(name, config) {
    this.Guid = core.guid();
    this.Name = this.types[name] === undefined ? 'axisBottom' : this.types[name];
    this.GeneratorConstructor = d3[this.Name];
    this.Stack = [];
    this.extendConfig(config);
    this._RangeWidth = this.Config._RangeWidth || 1.6;
  }

  // modify(config) {
  modifySelection() {
    core.modifySelection(this.SelectionD3, this.Config.modify);
  }

  attach(svg) {
    this.Svg = svg;
    this.G = svg.append('g');
    this.G.call(this.Generator); // append d3 axis
    this.SelectionD3 = this.G;
    this.SelectionRange = this.G.append('path').attr('class', 'range');
    this.data(this.Config, true);
    this.renderSelection(this.SelectionD3, this.SelectionRange, true);
  }

  // update Selection is no need
  data(config, noConvert) {
    const shouldConvert = noConvert !== true;
    if (shouldConvert) {
      this.Config = this.dataConvert(config);
    }
    this.modifySelection();
    this.setGenerator();
    /*
    this.setPosition(this.G);
    this.modifySelection(this.Config.modify);
    this.setGenerator();
    this.Generator(this.SelectionD3); // will transtion via d3*/
  }

  renderSelection(selectionD3, selectionRange, isFromAttach) {
    selectionD3.attr('transform', (d, i) => {
      return 'translate(' + this.fVal(this.Config.generator.x, d, i, 0) +
        ', ' + this.fVal(this.Config.generator.y, d, i, 0) + ')';
    });;
    !isFromAttach && this.Generator(selectionD3);
    selectionRange.attr('d', this._generatorRange());
    return selectionD3;
  }

  render() {
    const args = core.toArray(arguments);
    // board will call data(), so not update data here
    if (args.length > 0 && args[0] !== undefined && !DatabaseEvent.is1stMine(args)) {
      this.data.apply(this, args);
    }
    // this.setPosition(this.G);
    this.renderSelection(this.SelectionD3, this.SelectionRange);
  }

  transition() {
    const orginArgs = core.toArray(arguments),
      isFromBoard = DatabaseEvent.is1stMine(orginArgs),
      args = isFromBoard ? orginArgs[0].Args : orginArgs;
    let config;
    if (isFromBoard) {
      config = args && args.length > 0 && args[0] !== undefined ? args[0] : {};
    } else {
      config = orginArgs.length > 1 ? orginArgs.shift() : {};
      if (orginArgs.length > 0 && orginArgs[0] !== undefined) {
        this.data.apply(this, orginArgs);
      }
    }

    let transitionD3 = core.callInner(this.SelectionD3.transition(), config),
      transitionRange = core.callInner(this.SelectionRange.transition(), config);

    return this.renderSelection(transitionD3, transitionRange);
  }

  setGenerator() {
    const axis = this.GeneratorConstructor(),
      config = this.Config.generator;
    for (let name in config) {
      if (typeof axis[name] === 'function') {
        if (this.applyList[name] && core.isArray(config[name]) && config[name].length > 0) {
          axis[name].apply(axis, config[name]);
        } else {
          axis[name](config[name]);
        }
      }
    }
    this.Generator = axis;
  }

  _sort(a, b) {
    return a - b;
  }

  _generatorRange() {
    const plus = this._RangeWidth / 2,
      range = this.Config.generator.range,
      realRange = (core.isArray(range) && range.length > 1) ? range : this.Config.generator.scale.range();
    realRange[0] += plus;
    realRange[1] += plus;
    if (this.Name === 'axisTop' || this.Name === 'axisBottom') {
      return ['M', realRange[0], ',', plus + 'H' + realRange[1]].join('');
    } else {
      return ['M', plus, ',', realRange[0] + 'V' + realRange[1]].join('');
    }
  }

}

Axis.prototype.ShapeName = 'axis';
Axis.prototype.IsStatic = true;
Axis.prototype.applyList = {
  ticks: 'ticks',
  tickFormat: 'tickFormat'
};
Axis.prototype.types = {
  x: 'axisBottom',
  y: 'axisLeft',
  axisTop: 'axisTop',
  axisRight: 'axisRight',
  axisBottom: 'axisBottom',
  axisLeft: 'axisLeft'
};
Axis.prototype.defaultConfig = {
  generator: {
    tickSize: 0,
    tickPadding: 10
    // range
  },
  modify: {
    classed: {
      'md-chart-axis': true
    },
    style: {
      color: '#858688'
    }
  }
};

export default Axis;

